import React, {PureComponent} from 'react';
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as DongtaiActions from '../../../actions/DongtaiActions'
import {RefreshControl, ListView, NavBar, Toast, Popover,Icon} from 'antd-mobile'
import './quanDongtai.less'
import {Natives} from '../../../constants/index'
import Divider from "../../../components/Divider";
import create_circle from '../../../../imgs/quanzi/create_circle.png'
import add_circle from '../../../../imgs/quanzi/add_circle.png'
import add from '../../../../imgs/dongtai/add.png'
import back from '../../../../imgs/common/back.png'
import touxiang from '../../../../imgs/dongtai/touxiang.png'
import {createHashHistory} from 'history'
import DongtaiItem from '../../../components/DongtaiItem/index'
import {LocalStorageUtil} from '../../../util/index'
import Header from './header'
import Comment from '../../../components/Comment/index'
import CarouselView from '../../../components/Carousel/index'



const history = createHashHistory();
// let quan={};
class QuanDongtai extends PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            showPop: false,
            selectItem1: "select-item-selected",
            selectItem2: "select-item",
            selectItemTv1: "font-28 color-blue",
            selectItemTv2: "font-28 color-333",
        }
    }
    _add= ()=>{
        const {actions, state} = this.props;
        // 选择的那个圈的信息
        // const selectItem = LocalStorageUtil.getQuanItem();
        // console.log(selectItem);
        const {name, roomID} = state.quan;
        // LocalStorageUtil.clearQuanItem(); // 取完清除
        try {
            // 通知native跳转界面
            window.dsBridge.call(Natives.ADD_DONGTAI_FROM_JS, {circlename:name, circlenum:roomID}, function (responseData) {
                console.log("callback from native-->" + responseData);
                var obj = eval('(' + responseData + ')');
                if (obj.status === 100) {
                    // 刷新列表
                    console.log("刷新列表");
                    actions.refreshIng(); // refreshing = true 会触发onRefresh
                }
            });
        } catch (error) {
            console.log(error)
        }
    }

    _renderSelect= (index)=>{
        if(index == 0){
            this.setState({
                selectItem1: "select-item-selected",
                selectItem2: "select-item",
                selectItemTv1: "font-28 color-blue",
                selectItemTv2: "font-28 color-333",
            });
        }else {
            this.setState({
                selectItem2: "select-item-selected",
                selectItem1: "select-item",
                selectItemTv2: "font-28 color-blue",
                selectItemTv1: "font-28 color-333",
            });
        }
    }

    _renderRow = (rowData, sectionID, rowID)=>(
        <DongtaiItem
            key={rowData.trendsid}
            {...this.props}
            item= {rowData}
            // onItemClick= {()=>{this._onItemClick(rowData)}}
        />
    )
    _onItemClick = (item)=>{
        alert(item.name)
    }

    _backClick= ()=>{
        LocalStorageUtil.clearQuanItem();
        try {
            // 通知native跳转界面
            window.dsBridge.call(Natives.CLOSE_PAGE_FROM_JS, {}, function (responseData) {
                console.log("callback from native-->" + responseData);
            });
        } catch (error) {
            console.log(error)
        }
    }
    _chat=()=>{
        try {
            // 通知native跳转界面
            window.dsBridge.call(Natives.CHAT_FROM_JS, {name: this.props.state.quan.name}, function (responseData) {
                console.log("callback from native-->" + responseData);
            });
        }catch (error){
            console.log(error)
        }
    }
    _renderSpace = (sectionID, rowID) => (
        <div
            key={`${sectionID}-${rowID}`}
            style={{
                backgroundColor: '#F5F5F9',
                height: '0.25rem',
            }}
        />
    );
    _renderHeader= ()=>(<Header/>)
    _renderFooter = ()=>{
        const { state } = this.props;
        return(  <div style={{ padding: 30, textAlign: 'center' }}>
            {state.canLoad ? '加载中...' : '已全部加载完毕'}
        </div>);
    }
    _onRefresh = () => {
        const { state, actions } = this.props;
        console.log('onRefresh执行');
        if (!state.refreshing) {
            actions.refreshIng();
        }
        actions.getAllTrends(false, 1, 1, state.quan.roomID);
    };
    _onLoad = () => {
        const {state, actions } = this.props;
        if(state.loading || !state.canLoad){
            return;
        }
        actions.loadIng();
        if(state.canLoad){
            console.log('onLoad执行');
            actions.getAllTrends(true, state.pageSize+1, 1);
        }else
            console.log('onLoad未执行');
    };
    _getInputValue= (event)=>{
        const { actions } = this.props;
        actions.changeComment(event.target.value)
    }

    _sendComment= ()=>{
        const { state, actions } = this.props;
        if(state.commentStr && state.commentStr.length > 0){
            console.log(state.commentStr);
            actions.addCommentOrLike({
                trendsid: state.selectedItem.trendsid,
                token: LocalStorageUtil.getUserToken(),
                name: LocalStorageUtil.getName().length===0 ? LocalStorageUtil.getUserName():LocalStorageUtil.getName(),
                type: 1,
                contect: state.commentStr,
            });
        }else
            Toast.fail("请输入评论",1);

    }

    componentDidMount() {
        console.log("djy--->" + "componentDidMount");
        const {state, actions} = this.props;
        let quan;

        try {
            window.dsBridge.register(Natives.SEND_QUAN_FROM_NATIVE,function(jsonQuan){
                console.log("djy--->" + "接收到jsonQuan");
                console.log("djy--->" + jsonQuan);
                quan =  eval('(' + jsonQuan + ')');
                actions.refreshQuan(quan);
                actions.refreshIng();
                actions.getQuanChengyuan(quan.roomID);
            })

        } catch (error) {
            console.log(error)
        }

        // 注册一个方法，使圈成员可以刷新
        try {
            window.dsBridge.register(Natives.REFRESH_QUAN_CHENGYUAN_FROM_NATIVE,function(){
                console.log("djy---REFRESH_QUAN_CHENGYUAN_FROM_NATIVE");
                console.log("djy---quan.roomID---" + quan.roomID);
                actions.getQuanChengyuan(quan.roomID);
            })
        } catch (error) {
            console.log(error)
        }
    }


    render() {
        const { state, actions } = this.props;
        console.log(state);
        // 选择的那个圈的信息
     //    const selectItem = LocalStorageUtil.getQuanItem();
     //    console.log(selectItem);
     //    const {name, description, roomID} = selectItem;
     // // LocalStorageUtil.clearQuanItem(); // 取完清除

        const {name, description, roomID} = state.quan;

        let offsetX = -10; // just for pc demo
        if (/(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)) {
            offsetX = -26;
        }
        return (
            <div>
                <NavBar
                    iconName={null}
                    mode="light"
                    onLeftClick={this._backClick}
                    leftContent={(
                        <div className="back-container">
                            <img className="back-iv" src={back}/>
                            <p className="color-blue back-tv">返回</p>
                        </div>)}
                    rightContent={(
                        <img onClick={this._add} src={add}/>

                    )}
                >
                    圈动态
                </NavBar>


                {/*标题栏底下的头部*/}
                <div className="header-quan-dongtai">
                    <div className="top">
                        <div className="head-container">
                            <img className="head" src={touxiang}/>
                        </div>

                        <div className="top-right">
                            <span className="font-36 color-000">{name}</span>
                            <div style={{display:"flex", alignItems:"center"}}>
                                <span className="font-26 color-aaa">成员：{state.usercount}个</span>
                                <span className="font-26 color-aaa" style={{marginLeft: '0.12rem', flex:"1"}}>动态：{state.total}个</span>

                                <div style={{display:"flex", alignItems:"center"}} onClick={this._chat}>
                                    <span className="font-26 color-666">圈聊</span>
                                    <Icon color="#666" type="right"/>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="quan-dongtai-select">
                        <div className={this.state.selectItem1} onClick={()=>{this._renderSelect(0)}} ><span className={this.state.selectItemTv1}>全部</span></div>
                        <div className={this.state.selectItem2} onClick={()=>{this._renderSelect(1)}}><span className={this.state.selectItemTv2}>知识</span></div>
                    </div>
                </div>

                <Divider/>

                <ListView
                    dataSource={state.dataSource.cloneWithRows(state.rows)}
                    renderRow={this._renderRow}
                    renderSeparator={this._renderSpace}
                    renderFooter={this._renderFooter}
                    // renderHeader={this._renderHeader}
                    initialListSize={20}
                    pageSize={4}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={200}
                    style={{
                        height: document.documentElement.clientHeight*0.8,
                        paddingTop: '0.13rem',
                        margin: "0.02rem 0",
                        overflow:'hidden',
                    }}
                    onEndReached={this._onLoad}
                    onEndReachedThreshold={10}
                    scrollerOptions={{ scrollbars: true }}
                    refreshControl={<RefreshControl
                        refreshing={state.refreshing}
                        onRefresh={this._onRefresh}
                    />}
                />

                <Comment show={state.showComment} commentStr={state.commentStr} onChange={this._getInputValue} onSend={this._sendComment} />
                <CarouselView {...this.props}/>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    state: state.dongtai,
})

const mapDispatchToProps = dispatch => ({
    actions: bindActionCreators(DongtaiActions, dispatch)
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(QuanDongtai)



