import React from 'react';
import ReactDOM from 'react-dom';
import './styles/app.less';
import registerServiceWorker from './registerServiceWorker';
import { applyMiddleware, createStore} from 'redux';
import reducers from './js/redux/index'
import { Provider } from 'react-redux';
import Quanzi from './js/containers/quanzi/index'
import QuanDongtai from './js/containers/quanzi/dongtai/quanDongtai'
import QuanJieshao from './js/containers/quanzi/description/quanDescription'
import thunk from 'redux-thunk';
import {HashRouter as Router,Route,Link} from 'react-router-dom'

const store = createStore(reducers, applyMiddleware(thunk));

const Root = () => (
    <Router>
        <div>
            <Route exact path="/" component={Quanzi}/>
        </div>
    </Router>
)

ReactDOM.render(
    <Provider store={store}>
        <Root/>
    </Provider>,

    document.getElementById('root'));
registerServiceWorker();



