import React, {PureComponent} from 'react';
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as QuanziActions from '../../actions/QuanziActions'
import {Button, RefreshControl, ListView, NavBar, Icon, Toast, Popover} from 'antd-mobile'
import hanbao from '../../../imgs/quanzi/description.png'
import create_circle from '../../../imgs/quanzi/create_circle.png'
import add_circle from '../../../imgs/quanzi/add_circle.png'
import touxiang from '../../../imgs/dongtai/touxiang.png'
import './index.less'
import {Natives} from '../../constants/index'
import Divider from "../../components/Divider";
import {LocalStorageUtil} from '../../util/index'
import {createHashHistory} from 'history'

const Item = Popover.Item;
const history = createHashHistory();

class Quanzi extends PureComponent {

    _onSelectPop = (opt) => {
        console.log(opt.props.value);
        this.props.actions.togglePop(false);
        this._popOpration(opt.props.value);
    };
    _handleVisibleChange = (visible) => {
        this.props.actions.togglePop(visible);
    };
    _popOpration = (select) => {
        const {actions} = this.props;
        let fucName = select === "create_circle" ? Natives.CREATE_QUAN_FROM_JS : Natives.ENTER_QUAN_FROM_JS;
        try {
            // 通知native跳转界面
            window.dsBridge.call(fucName, {}, function (responseData) {
                console.log("callback from native-->" + responseData);
                var obj = eval('(' + responseData + ')');
                if (obj.status === 100) {
                    // 刷新列表
                    console.log("刷新列表");
                    actions.refreshIng(); // refreshing = true 会触发onRefresh
                }
            });
        } catch (error) {
            console.log(error)
        }
    };

    _renderRow = (rowData, sectionID, rowID) => (
        <div key={`${sectionID}-${rowID}`} className="quan-item-container" onClick={()=>{this._onItemClick(rowData)}}>
            <div className="left">
                <img className="head" src={touxiang}/>
            </div>
            <div className="right">
                <p className="font-36 color-333">{rowData.naturalName}</p>
                <p className="font-24 color-aaa" style={{marginTop: '0.12rem'}}>{rowData.description}</p>
            </div>
        </div>
    )
    _onItemClick = (item) => {
        LocalStorageUtil.saveQuanItem(item);
        setTimeout(()=>{
            try {
                // 通知native跳转界面
                window.dsBridge.call(Natives.NEW_QUAN_COMMON_WEB_FROM_JS, {url:"quanDongtai.html", quan: item}, function (responseData) {
                    console.log("callback from native-->" + responseData);
                });
            } catch (error) {
                console.log(error)
            }
        }, 200);
    }
    _renderSpace = (sectionID, rowID) => (
        <div
            key={`${sectionID}-${rowID}`}
            style={{
                backgroundColor: '#F5F5F9',
                height: '0.25rem',
            }}
        />
    );

    _onRefresh = () => {
        const {state, actions} = this.props;
        console.log('onRefresh执行');
        if (!state.refreshing) {
            actions.refreshIng();
        }
        actions.getMyCircle();
    };

    componentDidMount() {
        console.log("componentDidMount")
        const {actions} = this.props;
        actions.refreshIng(); // refreshing = true 会触发onRefresh

        try {
            window.dsBridge.register(Natives.REFRESH_QUAN_FROM_NATIVE,function(){
                actions.refreshIng();
            })
        } catch (error) {
            console.log(error)
        }
    }

    render() {
        const {state, actions} = this.props;
        let offsetX = -10; // just for pc demo
        if (/(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)) {
            offsetX = -26;
        }
        return (
            <div>
                <NavBar
                    iconName={null}
                    mode="light"
                    rightContent={
                        <Popover
                            mask
                            overlayClassName="fortest"
                            overlayStyle={{color: 'currentColor'}}
                            visible={state.showPop}
                            overlay={[
                                (<Item key="4" value="create_circle" icon={<img src={create_circle}/>}
                                       style={{margin: '0 0.1rem'}} className="font-26 color-333">创建圈子</Item>),
                                (<Item key="5" value="enter_circle" icon={<img src={add_circle}/>}
                                       style={{margin: '0 0.1rem'}} className="font-26 color-333">加入圈子</Item>),
                            ]}
                            align={{
                                overflow: {adjustY: 0, adjustX: 0},
                                offset: [offsetX, 15],
                            }}
                            onVisibleChange={this._handleVisibleChange}
                            onSelect={this._onSelectPop}>
                            <div style={{
                                height: '100%',
                                padding: '0 0.3rem',
                                marginRight: '-0.4rem',
                                display: 'flex',
                                alignItems: 'center',
                            }}>
                                <img src={hanbao}/>
                            </div>
                        </Popover>}>圈子</NavBar>

                <Divider/>
                <ListView
                    dataSource={state.dataSource.cloneWithRows(state.results)}
                    renderRow={this._renderRow}
                    renderSeparator={this._renderSpace}
                    initialListSize={20}
                    pageSize={8}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={200}
                    style={{
                        height: document.documentElement.clientHeight * 0.95,
                        padding: '0.13rem 0',
                        margin: "0.02rem 0",
                        overflow: 'hidden',
                    }}
                    scrollerOptions={{scrollbars: true}}
                    refreshControl={<RefreshControl
                        refreshing={state.refreshing}
                        onRefresh={this._onRefresh}
                    />}
                />

            </div>
        );
    }
}

const mapStateToProps = state => ({
    state: state.quanzi,
})

const mapDispatchToProps = dispatch => ({
    actions: bindActionCreators(QuanziActions, dispatch)
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Quanzi)



