import {
    REFRESH_ING,
    GET_MY_CIRCLE,
    ERROR,
    TOGGLE_POP,
} from './ActionsTypes'
import 'whatwg-fetch'
import {Toast} from 'antd-mobile'
import {APIs} from '../constants/index'
import {FetchUtil, LocalStorageUtil} from '../util/index'


export const refreshIng = () => ({ type: REFRESH_ING })
export const togglePop = (showPop) => ({ type: TOGGLE_POP, res:{showPop: showPop} })


export const getMyCircle = () => {
    console.log("getMyCircle");
    const req = {
        Token: LocalStorageUtil.getUserToken(),
        // Token: "3MJ2BKL4536Nb8946a16-4939-40c6-93d9-fa9741af315aX4T3WE4232Se10adc3949ba59abbe56e057f20f883eA4Z6BC50RIE0HD98TQW4EE180OG952",
    };

    return (dispatch, getState) =>{
        return fetch(FetchUtil.getGetReq(APIs.GET_MY_CIRCLE, req))
            .then((response) => response.json())
            .then((res) => {
                console.log(res);
                const {status, message} = res;
                if(status === 100){
                    dispatch({
                        type: GET_MY_CIRCLE,
                        res:  res,
                    });
                }else {
                    dispatch({type: ERROR});
                    Toast.fail(message, 1);
                }
            })
            .catch((error) => {
                dispatch({type: ERROR});
                Toast.fail(error, 1);
            })
    }
}



